import { Option, some, none, fromNullable } from 'fp-ts/lib/Option';

export default class CanvasLine {
    constructor(
        readonly _line: string,
        readonly _fontSize: number,
        readonly _fontName: string,
        readonly _canvasId: string,
        private _canvas: Option<HTMLCanvasElement> = none,
        private _context: Option<CanvasRenderingContext2D> = none,
        private _imageData: Option<ImageData> = none) {
            // キャンバスとコンテクストの取得
            this._canvas = fromNullable(document.getElementById(this._canvasId) as HTMLCanvasElement);
            this._context = this._canvas.fold(none, (canvas: HTMLCanvasElement) => {
                return fromNullable(canvas.getContext('2d'));
            });

            // フォントの指定
            this._context.map((ctx: CanvasRenderingContext2D) => {
                ctx.font = this._fontSize + 'px ' + this._fontName;
            });

            // キャンバスのクリア
            this._canvas.map((canvas: HTMLCanvasElement) => {
                this._context.map((ctx: CanvasRenderingContext2D) => {
                    ctx.clearRect(0, 0, canvas.width, canvas.height);
                });
            });
    }

    public draw(): void {
        this._context.map((context: CanvasRenderingContext2D) => {
            context.fillText(this._line, 0, this._fontSize);
            if (this.width !== 0) {
                this._imageData = some(context.getImageData(0, 0, this.width, this.height));
            }
        });
    }

    public get canvasId(): string {
        return this._canvasId;
    }

    public get height(): number {
        return this._fontSize * 2;
    }

    public get width(): number {
        return this._context.fold(0, (ctx: CanvasRenderingContext2D) => {
            return ctx.measureText(this._line).width;
        });
    }

    public get imageData(): Option<ImageData> {
        return this._imageData;
    }
}
