




















































































import { Component, Vue, Watch } from 'vue-property-decorator';
import { Option, some, none, fromNullable } from 'fp-ts/lib/Option';
import { fromOptions } from 'fp-ts/lib/These';
import CanvasLine from '../domain/model/CanvasLine';
import AABuilder from '../domain/service/AABuilder';

@Component
export default class Home extends Vue {
  private text: string = '';
  private aaText: string = '';
  private sampleAaText: string = '';
  private textSize: number = 14;

  private foregroundEmoji: string = '';
  private defaultForegroundEmoji = ':black_square:';
  private backgroundEmoji = '';
  private defaultBackgroundEmoji = ':white_square:';

  private fontOptions = [
      {value: 'Verdana', text: 'Verdana'},
      {value: 'monospace', text: 'monospace'},
      {value: 'Courier', text: 'Courier'},
      {value: 'Hack Regular', text: 'Hack Regular'},
      {value: 'MSゴシック', text: 'MSゴシック'},
      {value: 'ヒラギノ明朝 ProN W6', text: 'ヒラギノ明朝 ProN W6'},
      {value: 'ヒラギノ角ゴ Std W8', text: 'ヒラギノ角ゴ Std W8'},
  ];
  private selectedFont = this.fontOptions[0].value;

  private canvasImageUrl: string = '';
  private lineCanvas: HTMLCanvasElement[] = [];
  private lineContext: CanvasRenderingContext2D[] = [];
  private canvas: Option<HTMLCanvasElement> = none;
  private context: Option<CanvasRenderingContext2D> = none;

  protected updated(): void {
    const canvasLines = this.getCanvasLines();

    this.aaText = this.toAA(canvasLines, this.foregroundEmoji, this.backgroundEmoji);
    this.sampleAaText = this.toAA(canvasLines, '⬛️', '⬜️');
  }

  protected beforeUpdate(): void {
    this.draw();

    // ForegroundEmojiとBackgroundEmojiが空文字列になっていないか調査
    if (this.foregroundEmoji === '') {
      this.foregroundEmoji = this.defaultForegroundEmoji;
    }
    if (this.backgroundEmoji === '') {
      this.backgroundEmoji = this.defaultBackgroundEmoji;
    }
  }

  private draw(): void {
    this.getCanvasLines().forEach((cls: CanvasLine) => cls.draw());
  }

  private getCanvasLines(): CanvasLine[] {
    const lines = this.text.split('\n');
    return lines.map((line: string, index: number) => {
      const id = 'canvas' + index.toString();
      return new CanvasLine(line, this.textSize, this.selectedFont, id);
    });
  }

  private onClick(): string {
    this.$notify({
      group: 'note',
      title: 'Copied to clipboard.',
    });
    return this.aaText;
  }

  private toAA(canvasLines: CanvasLine[], foregroundEmoji: string, backgroundEmoji: string): string {
    return canvasLines.map((canvasLine: CanvasLine) => {
      canvasLine.draw();
      const aa = canvasLine.imageData.fold('', (imageData: ImageData) => {
        return new AABuilder(imageData, foregroundEmoji, backgroundEmoji).build();
      });
      return aa;
    }).join('\n');
  }
}
