import { dropWhile, reverse, head, snoc } from 'fp-ts/lib/Array';

export default class AABuilder {
    private readonly BLACK = 0;
    private readonly WHITE = 255;
    private readonly GRAY = (this.BLACK + this.WHITE) / 2;

    constructor(private imageData: ImageData, readonly emoji = ':emoji:', readonly blank = ' ') {}

    public build(): string {
        const imageArray = this.addMargin(this.trimMargin(this.imageArray));
        return imageArray.map((line: number[]) => {
            return line.map((color: number) => {
                if (color === this.BLACK) {
                    return this.emoji;
                } else {
                    return this.blank;
                }
            }).join('');
        }).join('\n');
    }

    private addMargin(imageArray: number[][]): number[][] {
        return head(imageArray).fold(imageArray, (_imageArray: number[]) => {
            const whiteLine = _imageArray.map((_: number) => this.WHITE);
            return snoc(imageArray, whiteLine);
        });
    }

    private trimMargin(imageArray: number[][]): number[][] {
        const lineIsBlank = (_line: number[]) => {
            return _line.every((color: number) => color === this.WHITE);
        };

        const trimHeadMargin = (_lines: number[][]) => {
            return dropWhile(_lines, lineIsBlank);
        };

        const trimEndMargin = (_lines: number[][]) => {
            const reversed = reverse(_lines);
            const trimmed = trimHeadMargin(reversed);
            return reverse(trimmed);
        };

        return trimHeadMargin(trimEndMargin(imageArray));
    }

    private get imageArray(): number[][] {
        /**
         * 返り値の例
         * [
         *  [WHITE, WHITE, WHITE, ..., WHITE]
         *  [WHITE, BLACK, WHITE, ..., BLACK]
         *  ...
         *  [WHITE, BLACK, BLACK, ..., BLACK]
         * ]
         */
        const toBin = (color: number) => color < this.GRAY ? this.WHITE : this.BLACK;

        const result = [];
        for (let i = 0; i < this.imageData.height; i += 1) {
            const line = [];
            for (let j = 0; j < this.imageData.width; j += 1) {
                const index = (j + i * this.imageData.width) * 4;
                const c = this.imageData.data[index] +
                    this.imageData.data[index + 1] +
                    this.imageData.data[index + 2] +
                    this.imageData.data[index + 3];
                line.push(toBin(c));
            }
            result.push(line);
        }
        return result;
    }
}
